#aboutContainer {
  flex-direction: column;
}
.info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  grid-gap: 2em;
  gap: 2em;
  flex-direction: row;
  align-items: unset;
  padding-top: 2vh;
}

.about-photo {
  max-width: 350px;
  border-radius: 5%;
  box-shadow: inset 0 3px 6px rgb(76 80 177), 0 4px 6px rgb(79 98 221 / 45%);
}
.about-details {
  flex: 40% 1;
  /* margin: 10px; */
  padding: 10%;
  font-size: 1.5em;
  text-align: left;
  /* box-shadow: inset 0 0 0 2px #7e5ef4; */
  box-shadow: inset 0 3px 6px rgb(76 80 177), 0 4px 6px rgb(79 98 221 / 45%);
  color: #070313;
  display: inline-block;
  vertical-align: middle;
  border-radius: 5%;
}

.social {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 50%;
  align-items: center;
  margin: 1vh auto;
}
.social svg {
  width: 2em;
  height: 2em;
  cursor: pointer;
}

.contact-form {
  display: flex;
  /* flex-wrap: wrap; */
  flex-direction: column;
  /* text-align: left; */
  justify-content: center;

  align-items: center;
  align-content: right;
  background-color: #ebdef7;
  /* padding: 10vh 15%; */
  border-radius: 5%;
  width: 80%;
  height: 100vh;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
h4 {
  text-align: center;
}
.form-group {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 60%;
}
.fullname {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0;
  width: 80%;
}
input[type="text"],
.input-email,
.text-content {
  flex: 1 1;
  border: 1px solid #ccc;
  background: #fff;

  /* margin: 0 0 5px; */
  padding: 10px;
  border-radius: 10px;
  width: 80%;
}
input[type="text"] {
  margin: 0.5rem 1rem;
}
.input-email,
.text-content {
  margin: 0.5rem 1rem;
  /* padding: 0; */
  /* width: 95%; */
}
.btn-submit {
  margin: 1rem 1rem;
  padding: 0.5rem;
  /* width: 30%; */
  font-weight: 600;
  border-radius: 10px;
  margin-bottom: 7px;
  background-color: #4543d8;

  padding: 0.4rem 0.7rem;
  margin-right: 5px;
  transition: all 300ms ease-out;
  color: #fff;

  transition: all 300ms ease-out;
}

/* .project-section {
  height: auto;
  margin-top: 7%;
} */

.projects-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
  /* flex-wrap: wrap; */
  flex-basis: auto;
  /* padding-top: 10vh; */
}
.project {
  /* flex: 0 4 50%; */
  width: 80%;
  margin: 1rem;
  border: 1px solid #4543d8;
  border-radius: 5%;
  /* border: 2px solid rgb(126, 88, 231); */
  box-shadow: inset 0 3px 6px rgb(76 80 177), 0 4px 6px rgb(79 98 221 / 45%);
  color: rgb(3, 3, 3);
  text-align: center;
  flex-direction: row;
}

.project img {
  width: 30%;
  object-fit: contain;
}
.project-description {
  width: 70%;
}
.desription-tags {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 50%;
}
.btn {
  margin-bottom: 7px;
  background-color: #4543d8;
  text-decoration: none;
  padding: 0.4rem 0.7rem;
  margin-right: 5px;
  transition: all 300ms ease-out;
  color: #fff;
  font-weight: 500;
  border-radius: 9px;
  transition: all 300ms ease-out;
}

